.header {
  background-color: #4CB8B3;
}
.header.fijo {
  left: 0;
  top: 0;
  position: fixed;
  width: 100%;
  box-shadow: 0.1rem 0.2rem 0.3rem rgba(0, 0, 0, 0.5);
  z-index: 999;
}
.header h1 {
  color: #FFFFFF;
  text-align: center;
  margin: 0;
  padding: 2rem 0;
}
.header .contenido-header {
  padding: 2rem;
}
@media (min-width: 768px) {
  .header .contenido-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 0;
  }
}

@media (min-width: 768px) {
  .navegacion-principal {
    display: flex;
    gap: 2rem;
    align-items: center;
  }
}
.navegacion-principal a {
  display: block;
  text-align: center;
  color: #FFFFFF;
  font-size: 2.4rem;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .navegacion-principal a {
    margin: 0;
  }
}
.navegacion-principal a:hover {
  color: #FEF46D;
}